import React, { useEffect } from 'react'
import { Carousel } from 'materialize-css/dist/js/materialize.min.js'


function DefaultCarousel() {

    useEffect(()=>{
        const defaultCaro = document.querySelector('.default-caro');
        let instance =  Carousel.init(defaultCaro,{
            fullWidth: true,
            indicators: true,
            duration: 400
        })

        setInterval(()=>{
            instance.next()
        },4000)

        document.querySelector('.btn-right').addEventListener('click',()=>{
            instance.next();
        })

        document.querySelector('.btn-left').addEventListener('click',()=>{
            instance.prev();
        })

    })

    return (
        <div className="carousel carousel-silder default-caro">
            <a className="carousel-item" href="#one!">
                <div className="caro-item" id="caro-1">
                    <h4>Lets Re-create the Beauty of India With Travel Raasta</h4>
                    <p>What Are You Waiting For ?? Pack Your Bags Now and get started</p>
                </div>
            </a>
            <a className="carousel-item" href="#two!">
                <div className="caro-item" id="caro-2">
                    <h4>Counting Miles to Reach the Adventure with Travel Raasta</h4>
                    <p>Wanna have some me time ?? To Explore The Inner Yourself</p>
                </div>
            </a>
            <a className="carousel-item" href="#three!">
                <div className="caro-item" id="caro-3">
                    <h4>Let's Meet and Conference in Leisure with Travel Raast</h4>
                    <p>Spend some quality time with your family and friends</p>
                </div>
            </a>  

            <button className="caro-button btn-right black">
                <i className="material-icons">keyboard_arrow_right</i>
            </button>

            <button className="caro-button btn-left black">
                <i className="material-icons">keyboard_arrow_left</i>
            </button>

        </div>
    )
}

export default DefaultCarousel
